import React , { Component } from 'react';


    const img0 = require ('../assets/card.png');
    const img1 = require ('../assets/card_blue.png');
    const img2 = require ('../assets/card_red.png');
    const img3 = require ('../assets/card_yellow.png');
    const img4 = require ('../assets/card_bomb.png');

class Card extends Component {
    constructor () {
        super();
        this.state = {   index: 0, img : img0 };    
    };
      
    ChangeCard = () => {
        let matrixImg = img1;
        switch(this.props.color) {
            case 'blue':
                matrixImg = img1;
              break;
            case 'red':
                matrixImg = img2;
              break;
            case 'yellow':
                matrixImg = img3;
              break;
            case 'black':
                matrixImg = img4;
              break;
            default:
              matrixImg = img0;
          }       
          this.setState ( {img : matrixImg } );
    }
    render () {                
        return (
            <div className='container'>                
                <img src={this.state.img} onClick={this.ChangeCard} alt='card' className='card' title={this.props.title} ></img>
            <div className='centered'>{this.props.title}</div>
          </div>      
        )
   }
}

export default Card;