import React , { Component } from 'react'



class Matrix extends Component {
    
    getClassName = (i) => {
        let className = '';
        switch (this.props.matrix[i]){
            case 'red': 
            className = 'table-danger';
            break;
            case 'blue': 
            className = 'table-primary'
            break;
            case 'yellow': 
            className = 'table-warning'
            break;
            case 'black': 
            className = 'table-dark'
            break;
            default:
            className = 'table-warning';
        }
        return className;
    };
    render () {
        return (
            <div>
             

                <table className="table table-sm table-bordered">
                    <tbody>
                        <tr>
                            <td className={this.getClassName(0)}>CN</td>
                            <td className={this.getClassName(1)}>CN</td>
                            <td className={this.getClassName(2)}>CN</td>
                            <td className={this.getClassName(3)}>CN</td>
                            <td className={this.getClassName(4)}>CN</td>
                        </tr>
                        <tr>
                            <td className={this.getClassName(5)}>CN</td>
                            <td className={this.getClassName(6)}>CN</td>
                            <td className={this.getClassName(7)}>CN</td>
                            <td className={this.getClassName(8)}>CN</td>
                            <td className={this.getClassName(9)}>CN</td>
                        </tr>
                        <tr>
                            <td className={this.getClassName(10)}>CN</td>
                            <td className={this.getClassName(11)}>CN</td>
                            <td className={this.getClassName(12)}>CN</td>
                            <td className={this.getClassName(13)}>CN</td>
                            <td className={this.getClassName(14)}>CN</td>
                        </tr>
                        <tr>
                            <td className={this.getClassName(15)}>CN</td>
                            <td className={this.getClassName(16)}>CN</td>
                            <td className={this.getClassName(17)}>CN</td>
                            <td className={this.getClassName(18)}>CN</td>
                            <td className={this.getClassName(19)}>CN</td>
                        </tr>
                        <tr>
                            <td className={this.getClassName(20)}>CN</td>
                            <td className={this.getClassName(21)}>CN</td>
                            <td className={this.getClassName(22)}>CN</td>
                            <td className={this.getClassName(23)}>CN</td>
                            <td className={this.getClassName(24)}>CN</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        )
    }
}

export default Matrix;