import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import Card from './components/Card';
import Matrix from './components/Matrix';
import {getUniqueIds,getMatrixId,getGameIndex}  from './utils';
import CARDTITLES from './data/cardTitles';
import MATRIX_BLUE from './data/blueMatrix';
import MATRIX_RED from  './data/redMatrix';
import GAMES from './data/games'; 

const CardsInGame = 25;
const CardsDictionarySize = CARDTITLES.length-1;
const GamesAvailable = GAMES.length-1;
const MatrixAvailable = MATRIX_BLUE.length-1;
let CURRENT_GAME = [];

class App extends Component {

  constructor () {
    super ();
    this.state = { selectedColor: 'blue',gameIds: [], cards: [] , matrix: [] , showMatrix : false, gameID:''}
  }

  handleColorChange = (changeEvent) => 
  {
    this.setState({
      selectedColor: changeEvent.target.value
    });   
  }

  handleGameCodeEntered = (event) =>
  {
    this.setState ( {gameID : event.target.value} );
  } 

  showMatrix = () => {
    this.setState ( {showMatrix : !this.state.showMatrix} ); 
  }

  geCardsString = () => {
    let s = 'CARDS: ';
    this.state.cards.forEach ( (card) => {
      s+=  card + " ";
    });
    //s+=']';
    return s;
  }

  joinGame = () => {
    CURRENT_GAME.length = 0;
    const gameCode = this.state.gameID;
    const words = gameCode.split('-');

    const index = words[0];
    const matrixId =  words[1]; // PROBLEM - Colors change
    const gameIds = getUniqueIds (CardsInGame,CardsDictionarySize); // only to pass id's to the component so React component is refreshed
        
    CURRENT_GAME = GAMES[index];
    if (this.state.selectedColor === 'blue'){
      this.setState ( { gameIds: gameIds, cards: CURRENT_GAME, matrix:MATRIX_BLUE[matrixId] , showMatrix:false} );
    }
    if (this.state.selectedColor === 'red'){
      this.setState ( { gameIds: gameIds, cards: CURRENT_GAME, matrix:MATRIX_RED[matrixId] , showMatrix:false} );
    }
  }
 
  /*
    This method is used to select a previously built game
    and compose a CURRENT_GAME array.
  */ 
  getNewGame = () => {
      CURRENT_GAME.length = 0;
      let index = getGameIndex(GamesAvailable);
      const gameIds = getUniqueIds (CardsInGame,CardsDictionarySize); // only to pass id's to the component
      const matrixId =  getMatrixId (MatrixAvailable);
      const matId = matrixId.toString();
      const dash = '-'
      let gameid = index+dash+matId;
      CURRENT_GAME = GAMES[index];

      if (this.state.selectedColor === 'blue'){
        this.setState ( { gameIds: gameIds, cards: CURRENT_GAME, matrix:MATRIX_BLUE[matrixId], gameID: gameid , showMatrix:true} );
      }
      if (this.state.selectedColor === 'red'){
        this.setState ( { gameIds: gameIds, cards: CURRENT_GAME, matrix:MATRIX_RED[matrixId], gameID: gameid, showMatrix:true } );
      }
  }
  
  
  /*
    This method is used to generate the game on the fly by randonmly select 25 indexes
    and compose a CURRENT_GAME array.
  */ 
  generateNewGame = () => {
    CURRENT_GAME.length = 0;
    const gameIds = getUniqueIds (CardsInGame,CardsDictionarySize);
    const matrixId =  getMatrixId (MatrixAvailable);
    const matId = matrixId.toString();
    const dash = '-'
    let gameid = matId+dash+matId;
    
    
    gameIds.forEach ( (index) => {
      CURRENT_GAME.push (CARDTITLES[index]);
    });
    if (this.state.selectedColor === 'blue'){
      this.setState ( { gameIds: gameIds, cards: CURRENT_GAME, matrix:MATRIX_BLUE[matrixId], gameID: gameid , showMatrix:true} );
    }
    if (this.state.selectedColor === 'red'){
      this.setState ( { gameIds: gameIds, cards: CURRENT_GAME, matrix:MATRIX_RED[matrixId], gameID: gameid, showMatrix:true } );
    }    
  }

  render() {   
    const startIndex = 0;    
    let game = this.geCardsString ();
    return (      
      <div className="container-fluid">
        <hr></hr>
          <div className="row">          
            <div className="col">
              <table className="cards">
                <tbody>
                <tr>
                    <td><Card key={this.state.gameIds[0]} title={this.state.cards[0]} color={this.state.matrix[0]}/></td>
                    <td><Card key={this.state.gameIds[1]} title={this.state.cards[startIndex+1]} color={this.state.matrix[1]}/></td>
                    <td><Card key={this.state.gameIds[2]} title={this.state.cards[startIndex+2]} color={this.state.matrix[2]}/></td>
                    <td><Card key={this.state.gameIds[3]} title={this.state.cards[startIndex+3]} color={this.state.matrix[3]}/></td>
                    <td><Card key={this.state.gameIds[4]} title={this.state.cards[startIndex+4]} color={this.state.matrix[4]}/></td>
                </tr>
                <tr>
                    <td><Card key={this.state.gameIds[5]} title={this.state.cards[startIndex+5]} color={this.state.matrix[5]}/></td>
                    <td><Card key={this.state.gameIds[6]} title={this.state.cards[startIndex+6]} color={this.state.matrix[6]}/></td>
                    <td><Card key={this.state.gameIds[7]} title={this.state.cards[startIndex+7]} color={this.state.matrix[7]}/></td>
                    <td><Card key={this.state.gameIds[8]} title={this.state.cards[startIndex+8]} color={this.state.matrix[8]}/></td>
                    <td><Card key={this.state.gameIds[9]} title={this.state.cards[startIndex+9]} color={this.state.matrix[9]}/></td>
                </tr>
                <tr>
                    <td><Card key={this.state.gameIds[10]} title={this.state.cards[startIndex+10]} color={this.state.matrix[10]}/></td>
                    <td><Card key={this.state.gameIds[11]} title={this.state.cards[startIndex+11]} color={this.state.matrix[11]}/></td>
                    <td><Card key={this.state.gameIds[12]} title={this.state.cards[startIndex+12]} color={this.state.matrix[12]}/></td>
                    <td><Card key={this.state.gameIds[13]} title={this.state.cards[startIndex+13]} color={this.state.matrix[13]}/></td>
                    <td><Card key={this.state.gameIds[14]} title={this.state.cards[startIndex+14]} color={this.state.matrix[14]}/></td>
                </tr>
                <tr>
                    <td><Card key={this.state.gameIds[15]} title={this.state.cards[startIndex+15]} color={this.state.matrix[15]}/></td>
                    <td><Card key={this.state.gameIds[16]} title={this.state.cards[startIndex+16]} color={this.state.matrix[16]}/></td>
                    <td><Card key={this.state.gameIds[17]} title={this.state.cards[startIndex+17]} color={this.state.matrix[17]}/></td>
                    <td><Card key={this.state.gameIds[18]} title={this.state.cards[startIndex+18]} color={this.state.matrix[18]}/></td>
                    <td><Card key={this.state.gameIds[19]} title={this.state.cards[startIndex+19]} color={this.state.matrix[19]}/></td>
                </tr>
                <tr>
                    <td><Card key={this.state.gameIds[20]} title={this.state.cards[startIndex+20]} color={this.state.matrix[20]}/></td>
                    <td><Card key={this.state.gameIds[21]} title={this.state.cards[startIndex+21]} color={this.state.matrix[21]}/></td>
                    <td><Card key={this.state.gameIds[22]} title={this.state.cards[startIndex+22]} color={this.state.matrix[22]}/></td>
                    <td><Card key={this.state.gameIds[23]} title={this.state.cards[startIndex+23]} color={this.state.matrix[23]}/></td>
                    <td><Card key={this.state.gameIds[24]} title={this.state.cards[startIndex+24]} color={this.state.matrix[24]}/></td>
                </tr>
                </tbody>
              </table>
            </div>

            <div className="col col-lg-2">
              <p></p>
              <button type="button" className="btn btn-primary" onClick={this.getNewGame}>HOST A NEW GAME</button>
                  <p></p><p></p>
                          <form>
                            <div className="radio">
                              <label>
                                <input type="radio" value="blue" checked={this.state.selectedColor === 'blue'} onChange={this.handleColorChange} />
                                Blue Team Starts
                              </label>
                            </div>
                          <div className="radio">
                            <label>
                              <input type="radio" value="red" checked={this.state.selectedColor === 'red'} onChange={this.handleColorChange} />
                              Red Team Starts
                            </label>
                            </div>
                        </form>
                          <label>GAME ID</label> <span className="badge badge-secondary">{this.state.gameID}</span>
                        <p></p>
                        <p></p>
                        <hr /> 
                        <label >OR </label><p></p>
                        <button type="button" className="btn btn-primary" onClick={this.joinGame}>JOIN A GAME </button>
                          <p></p>
                          <div className="input-group input-group-sm mb-3">
                            <div className="input-group-prepend">
                              <span className="input-group-text" id="inputGroup-sizing-sm">GAME ID</span>
                            </div>
                            <input id="gameCode" type="text" className="form-control" aria-label="Sizing example input" aria-describedby="inputGroup-sizing-sm" value={this.state.gameID} onChange={this.handleGameCodeEntered}></input>
                          </div>
                        <hr></hr>
                        {
                          this.state.showMatrix ? (
                          <div>
                            <p></p>
                            <Matrix matrix = {this.state.matrix}/>
                          </div> ) : 
                          ( 
                          <div>
                          </div>
                          )
                        }
            </div>
          </div>
        <div>
          <hr />
          <p>
            {game}
          </p>
          <hr />
          <p>
          </p>
        </div>
      </div>

    )
    }
}

export default App;
