

export function getUniqueIds (totalToGet , sizeOfList){
    let listOfIds = [];
    let n = 0;
    while (n < totalToGet)
    {
      let index = Math.floor((Math.random() * sizeOfList));
      if (listOfIds.indexOf(index) < 0)
      {
        listOfIds.push (index);
        n++;
      }
    }
    return listOfIds;
  }
  
  export function getGameIndex (sizeOfList)
  {
    let index = Math.floor((Math.random() * sizeOfList));
    return index;
  }
  
  export function getMatrixId (sizeOfList){
    let index = Math.floor((Math.random() * sizeOfList));      
    return index;
  }
  
  
